body{
  background-color: #6363bf7a;
  margin:5px; 
}

.App {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100vh;
  
}

.App-header {
  background-color: #6363bf;
  overflow: visible;
  width: 68%;
  height: 18vh;
  text-align: center;
  justify-content: space-around;
  margin:auto;
  padding: 20px;
  color: white;
  align-self: center;
  border: dashed #db11af;
border-width: 2px 4px;
border-radius: 20px;
  
  font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;

}

.Messages-list {
  padding: 20px 0;
  max-width: 900px;
  width: 100%;
  margin: 0 auto;
  list-style: none;
  padding-left: 0;
  flex-grow: 1;
  overflow: auto;
  background-color: white;
  border-radius: 20px;

}


.Messages-message {
  display: flex;
  margin-top: 10px;
  
}

.Messages-message.currentMember {
  /*justify-content: flex-end;*/
  flex-direction: row-reverse;
  text-align: right;
  border: white;
}

.Message-content {
  display: inline-block;
  
}

.currentMember > .Message-content {
  align-items: flex-end;
}

.Messages-message > .avatar {
  height: 35px;
  width: 35px;
  border-radius: 50%;
  display: inline-block;
  margin: 0 10px -10px;
}

.Message-content > .username {
  display: block;
  color: white;
  font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
  font-size: 16px;
  padding-bottom: 4px;
  background-color: #6363bf;
  border-radius: 12px;
  max-width: -webkit-fit-content;
  max-width: -moz-fit-content;
  max-width: fit-content;
  padding: 5px;
}

.Message-content > .text {
  padding: 10px;
  max-width: 400px;
  margin: 0;
  border-radius: 12px;
  background-color: cornflowerblue;
  color: white;
  display: inline-block;
  
  font-size: 20px;
  font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
}

.currentMember > .Message-content .text {
  background-color: #db11af;
 
}

form {
  display: flex;
  width: 100%;
  justify-content: space-between;
  max-width: 900px;
  margin: 0 auto 40px;
}

input {
  padding: 5px;
  font-size: 18px;
  border-radius: 8px;
  border: 1px solid #6363bf70;
  flex-grow: 1;
  background-color: #6363bffd;
  color:white;
  outline: none;
  
}
input::-webkit-input-placeholder{
  color:rgb(99, 156, 156);
}
input:-ms-input-placeholder{
  color:rgb(99, 156, 156);
}
input::placeholder{
  color:rgb(99, 156, 156);
}

button {
  padding: 5px 10px;
  font-size: 16px;
  background-color: #6363bf;
  color: white;
  border: none;
  border: dashed #db11af ;
  border-width: 2px 4px;
  border-radius: 40px;
  margin-left: 10px;
}

